@mixin hover($_property, $_toValue) {
  transition: #{$_property} 0.3s;
  &:hover {
    #{$_property}: $_toValue;
  }
}

@mixin onDesktop {
  @media (min-width: 1024px) {
    @content;
  }
}

@mixin onBigPhone {
  @media (min-width: 450px) {
    @content;
  }
}

@mixin onTablet {
  @media (min-width: 768px) {
    @content;
  }
}

@mixin onBigDesktop {
  @media (min-width: 1440px) {
    @content;
  }
}

@mixin onTV {
  @media (min-width: 1920px) {
    @content;
  }
}
